import "./LogoutButton.css"
import exit from "../../images/exitIcon.svg"
import useResize from "../../hooks/useResize";
import {useHistory} from "react-router-dom";

const LogoutButton = () => {

    const windowSize = useResize();
    const history = useHistory();

    const logOut = () => {
        localStorage.removeItem("token");
        history.push("/antipoff-group-test/signup")
    }

    return (<div className="logout">
        {windowSize.width >= 860 ? <button onClick={logOut} className="logout_btn-l" type="button">
            
        </button> : <button onClick={logOut} className="logout_btn-s"><img src={exit} alt="exit"/></button>}
    </div>);
};

export default LogoutButton;